import React from 'react';
import { MainContainer, HeadGear } from 'Templates';
import { CommonSetting } from '../../CommonSetting';
import { localkey } from 'Source/App/Main/local-config';
import * as Section from './Sections';

const Freestyle = () => { 
	const title = 'Shadows';
	const context = `it is a style which allow your other components to look like neumorphism.`;

	return (
		<>  
            <HeadGear>
                <HeadGear.Meta 
					title={`${title} - Neumorphism Project | Mytabworks`}
					description={context}
					image='/images/icons/icon-512x512.png'
				/>
            </HeadGear>
			<MainContainer 
				title={title}
				context={context}
				main={<FreestyleMain />}
				side={<FreestyleSidePanel />}
				style={{ backgroundColor: 'transparent' }}
			/>
		</>
	);
};

Freestyle.propTypes = {};

const FreestyleMain = () => (
	<>
		<Section.FlatStyle /> 
        <Section.ConcaveStyle />
        <Section.ConvexStyle />
        <Section.EngraveStyle />
		<Section.FloatStyle />
		<Section.HoverStyle />
		<Section.NoShadow />
	</>
);

const FreestyleSidePanel = () => (
	<ul className='scroll-nav'>
		<li>
			<h6 className='px-3 py-2 mb-0'>Shadows Style</h6>
		</li>
		<CommonSetting
			localkey={localkey.freestyle}
			settingsAllowed={['distance', 'blur', 'intensity', 'border']}
		/>
	</ul>
);

export default Freestyle;
